/* Estilos generales */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Header */
header {
    background-color: #333;
    color: #fff;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header h1 {
    margin: 0;
}

nav ul {
    list-style: none;
    display: flex;
    gap: 1rem;
    margin: 0;
    padding: 0;
}

nav a {
    color: #fff;
    text-decoration: none;
}

nav a.active {
    border-bottom: 2px solid #fff;
}

/* Sections */
section {
    padding: 2rem;
}

section img {
    max-width: 150px;
    border-radius: 50%;
}

section h2 {
    margin-top: 0;
}

/* Footer */
footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 1rem 0;
    position: fixed;
    width: 100%;
    bottom: 0;
}

footer ul {
    list-style: none;
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin: 0;
    padding: 0;
}

footer a {
    color: #fff;
    text-decoration: none;
}

/* Portfolio Projects */
.project {
    margin-bottom: 2rem;
}

.project img {
    width: 100%;
    height: auto;
    display: block;
}